import React,{useState} from 'react'
import {useSelector} from 'react-redux'
import {currentEventSelector} from '../../Store/modules/evntStore'
import {currentHostSelector,} from '../../Store/modules/hostStore'
import InvitShare from '../../Components/InvitShare'
import { Button,Box,Grid,Typography} from '@material-ui/core'
import {useStyles} from '../../ConfigMaterial'
import Icon from '@material-ui/core/Icon'
import InvitForm from '../../Components/Dashboard/InvitForm'
import ListInvit from '../../Components/Dashboard/ListInvit'
import FormInvit from '../../Components/Dashboard/FormInvit'


const InvitId = () => {


  const classes = useStyles()
  
  const event = useSelector(currentEventSelector)

  const host = useSelector(currentHostSelector)

  const [openInvitForm,setOpenInvitForm] = useState(false)

  const [openFormInvit,setOpenFormInvit] = useState(false)
 
  const [listHost,setListHost] = useState(false)


  const toggleFormInvit = (val) => setOpenInvitForm(val)

  const closeInvitForm = () => setOpenFormInvit(false)

  const toggleHostList = () => setListHost(listHost => !listHost ? true : false)

  const field = {nom:"nom", prenom:"prenom" ,titre:"ajouter invit√©",mode:"share"} 

  return(

    <Grid container direction="row" justify="flex-start">
      {openFormInvit && <FormInvit Close={closeInvitForm} field={field} eventId ={event.eventId} uid={event.uid}/>}
       <Grid item md={4}>
          <Box>
            <Typography color="secondary" variant="h4">Invitation</Typography>   
            <div style={{width:'54%'}} className={classes.underLine}></div>
          </Box>
          <Box display="flex" mt={3} style={{width:"90%"}}>
            <Box>
              <Button variant="text" onClick={toggleHostList} color="primary" startIcon={<Icon color="primary">list</Icon>}>voir invit√©s</Button>
            </Box>
            <Box ml={1}>
              <Button variant="text" onClick={()=>setOpenFormInvit(true)} color="primary" startIcon={<Icon color="primary">supervised_user_circle</Icon>}>ajouter invit√©</Button>
            </Box>
          </Box>
          <Box style={{width:'90%'}} mt={1}>
            { listHost && <ListInvit toggleFormInvit={toggleFormInvit}/> } 
          </Box>
        </Grid>
      <Grid item md={4} >
        <Grid container  >
          <Grid item md={12}>
            {host.autreMembreCompleted && <InvitShare/>}
          </Grid>
        </Grid>
      </Grid>
      <Grid item md={3}  >
        <Grid container justify="center">
          <Box mt={3} pl={4}>
            {openInvitForm && <InvitForm toggleFormInvit={toggleFormInvit} />}
          </Box>
        </Grid> 
      </Grid>
    </Grid>
  )

}

export default InvitId