import React,{useState,useEffect} from 'react'
import {useSelector} from 'react-redux'
import {loadingSelector} from '../Store/modules/authStore'
import {  Grid, Typography,Box } from '@material-ui/core'
import {homeTitles} from '../Constants'
import Icon from '@material-ui/core/Icon'


const Home = () => {

    const loading = useSelector(loadingSelector)

    const [displayStyle,setDisplayStyle] = useState("none")

    useEffect(() => {

        const timer = setTimeout(() => { setDisplayStyle("")
            
        }, 1300);

        return()=> clearTimeout(timer)
    },[]);

 

    return( 

        <Grid container display="flex" justify="center" style={{display:displayStyle}} >
            {loading &&   
            
            <Grid item md={6} className='textColor'>
                <Box display="flex" flexDirection="column">
                    <Box mt={3}>
                        <Typography color='primary' variant='h3'>Bienvenue sur Wedd Easy </Typography>
                    </Box>
                    <Box mt={1}>
                        <Typography variant="h6">L'appli qui vous accompagne dans l'organisation de votre √©venement </Typography>
                    </Box>
                    <Box mt={5}>
                        {homeTitles.map((item,index) =>

                            <Box key={index} display="flex" mt={2}>
                                <Box mr={2}>
                                    <Icon>{item.icon}</Icon>
                                </Box>
                                <Typography variant="body1">{item.title}</Typography>
                            </Box>
                        )}
                    </Box>
                </Box>
            </Grid>}
        </Grid>
    )
}

export default Home