import React,{useState,useEffect} from 'react';
import {useSelector} from 'react-redux'
import {Link} from 'react-router-dom'
import {currentUserSelector,loadingSelector} from '../Store/modules/authStore'
import {navNoUser,navUser} from '../Constants'
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Navigation from './Navigation'
import Loader from './Loader'
import DrawerNav from './Drawer'
import { Hidden, Container,Box,Grid, Avatar } from '@material-ui/core';
import Icon from '@material-ui/core/Icon';
import {themeGlobal} from '../ConfigMaterial/index'


const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({

 
  small: {
    width: theme.spacing(4),
    height: theme.spacing(4),
    marginLeft:theme.spacing(2)
  },

  avatar:{

    backgroundColor:themeGlobal.palette.secondary.light
  },

  menuButton: {
    marginRight: theme.spacing(2),
  },

 
  appBar: {

    background: ' rgb(195,150,90) linear-gradient(0deg, rgba(270,20,50,0.8) 5%, rgba(300,90,96,1) 50%)', 
  },
  
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
  },
}));

const TopHeader = () => {


  const userLog = useSelector(currentUserSelector)

  const loading = useSelector(loadingSelector)

  const [open,setOpen] = useState(false)

  const [navItems,setNavitems] = useState()
  
  const [displayStyle,setDisplayStyle] = useState("none")


  useEffect(() => {

    userLog.username ? setNavitems(navUser) : setNavitems(navNoUser)

    setTimeout(() => {

      setDisplayStyle("")
      
    }, 1300);

  },[userLog])

  
  const handleOpenDrawer = () => {setOpen(true)}

  const handleCloseDrawer = () => {setOpen(false)}

  const classes = useStyles();

  const RenderAvatar = () => {

    if(userLog.username){

      return(

        <Link className="text-link" to="/Profile">
          <IconButton color='inherit'>
              <Typography color='inherit'>{userLog.username}</Typography>{
                userLog.avatar == null ? (<Avatar className={classes.avatar}><Icon>face</Icon></Avatar>) : 
                (<Avatar className={classes.small} src={userLog.avatar} ></Avatar>)
              }
          </IconButton>
        </Link>
      )
    } else {return null}
  }

  return (
    <div className={classes.root}>

      {loading ? (  

      <div style={{display:displayStyle}} >
      <AppBar position='fixed' 
       className={clsx(classes.appBar, {
        [classes.appBarShift]: open,
      })}>
        <Toolbar >
          <Grid container alignItems="center" direction="row" justify="space-between">
          {open && <DrawerNav navItems={navItems} close={handleCloseDrawer} open={open} />}
            <Grid item md={2}>
              {!open && 
              <Box display ="flex" alignItems="center">
                <IconButton onClick={handleOpenDrawer} edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                <MenuIcon />
                </IconButton>
                <Link className="text-link" to='/'>
                <Typography  variant="h6" className={classes.title}> Wedd Easy</Typography>
                </Link>
              </Box>}
            </Grid>
            <Grid item md={7}>
            {!open && 
              <Box display="flex" alignItems="center" justifyContent="flex-end">
                 <Hidden smDown>
                    <Navigation navItems={navItems}/>
                  </Hidden>
                <Box>
                  <RenderAvatar/>
                </Box>
              </Box>}
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar></div>) : (
      <Container>
        <div className='maxHeight flex'> 
          <Loader/>
        </div>
      </Container>
      )
      }
    </div>
  )
}

export default TopHeader

