import React from 'react'
import clsx from 'clsx';
import {useRouteMatch} from 'react-router-dom'
import {useDispatch,useSelector} from 'react-redux' 
import {selectedEventSelector,fetchCurrentEvent} from '../Store/modules/evntStore'
import {fetchHostById,currentHostSelector} from '../Store/modules/hostStore'
import {visitorHostSelector} from '../Store/modules/authStore'
import { CardActions, CardContent,Card,IconButton,Typography,Box,Grid,CardMedia,Collapse} from '@material-ui/core'
import Icon from '@material-ui/core/Icon'
import {useStyles} from '../ConfigMaterial'
import ListMembre from '../Components/Dashboard/ListMembre'
import {FacebookMessengerShareButton,FacebookMessengerIcon,EmailShareButton,EmailIcon,WhatsappShareButton,WhatsappIcon,ViberShareButton,ViberIcon} from "react-share"


const InvitShare = () => {

    const classes = useStyles()
    const dispatch = useDispatch()
    const match = useRouteMatch()

    const currentEvnt = useSelector(selectedEventSelector)

    const visitor = useSelector(visitorHostSelector)
    
    const host = useSelector(currentHostSelector)

    const [hostId,setHostId] = React.useState("")

    React.useEffect(() => {

        match.params.hostId ? setHostId(match.params.hostId) : setHostId(host.hostId)

    },[match,host.hostId])

    const id =  match.params.id

    const adress = `https://raffortville.github.io/WeddEasy/#/InvitPage/${id}/host/${hostId}`

    console.log(adress)

    const appId = "851151665420013"

    const description = currentEvnt.organisateur + " vous invite √† un √©venement le " + currentEvnt.date + " √† " + currentEvnt.titre 
    const objet = currentEvnt.description

    const [display,setDisplay] = React.useState("hidden")

    const [expanded, setExpanded] = React.useState(false)

    const [displayText,setDisplayText] = React.useState("block")


    React.useEffect(() => {

        dispatch(fetchCurrentEvent(id))
        dispatch(fetchHostById(hostId))
    
    },[dispatch,id,hostId])

    const handleExpandClick = () => {
         
        setExpanded(!expanded); 

        displayText === "block" ? setDisplayText('none') : setDisplayText('block')
    };

    setTimeout(() => { setDisplay("visible")},500)

    const RenderImage = () => {

        if(currentEvnt.image === null || undefined){

            return (null) 

        } else { return (<CardMedia className={classes.media}  image={currentEvnt.image} ></CardMedia>)}
    }

    console.log(adress)
    return(

       <Grid container style={{visibility: display}}  >
           <Grid item md={12}>
               <Box display="flex" justifyContent="center" alignItems="center" mt={3} >
                   <Card raised  className={classes.cardShare}>
                      {currentEvnt.image && RenderImage()}
                      <Box style={{display:displayText}}>
                        <CardContent >
                            <Typography color="primary" gutterBottom variant="h5" component="h2">{currentEvnt.titre}</Typography>
                            <Typography gutterBottom >Ch√®re <span>{host.nom}</span><span>  {host.prenom}</span></Typography>
                            <Typography variant="body2" color="textPrimary" component="p" style={{fontFamily:"cursive"}}>
                                {currentEvnt.organisateur} sont heureux de vous convier √† leur mariage, qui sera c√©l√©br√©
                                le {currentEvnt.date} √† {currentEvnt.lieu}. <br/> Nous vous prions de confirmer votre pr√©sence pour cet √©venement.
                                </Typography>
                        </CardContent>
                       </Box>
                       <CardActions>
                           <Box display="flex" alignItems="center" justifyContent="space-between" style={{width:"100%"}}>
                                <Box display="flex" alignItems="center" style={{width:"50%"}}>
                                   <IconButton 
                                    className={clsx(classes.expand, {
                                        [classes.expandOpen]: expanded,
                                    })}
                                    onClick={handleExpandClick}
                                    aria-expanded={expanded}
                                    aria-label="show more">
                                    <Icon>expand_less</Icon>
                                    </IconButton> 
                                    <Typography color="textSecondary">Membes invit√©s</Typography> 
                                </Box>
                                    {!visitor &&
                                        <Box display="flex" justifyContent="space-between" style={{width:"30%"}}>
                                            
                                            <FacebookMessengerShareButton  url={adress} appId={appId}>
                                                
                                                    <FacebookMessengerIcon size="20" round={true}/>
                                                
                                            </FacebookMessengerShareButton>
                                            <EmailShareButton url={adress} subject={objet}>
                                                
                                                    <EmailIcon size="20" round={true}/>
                                               
                                            </EmailShareButton>
                                            <WhatsappShareButton url={adress} title={description} separator=" ">
                                                
                                                    <WhatsappIcon round={true} size="20"/>
                                               
                                            </WhatsappShareButton>
                                            <ViberShareButton url={adress} title={description} separator=" ">
                                                
                                                    <ViberIcon size={20} round={true}/>
                                                
                                            </ViberShareButton>
                                        </Box>
                                    }
                           </Box>
                       </CardActions>
                       <Collapse in={expanded}  unmountOnExit style={{overflowY:"scroll"}}>
                          <ListMembre host = {host}/>
                       </Collapse>
                   </Card>
               </Box>
           </Grid>
       </Grid>
    )
}

export default InvitShare