import React,{useState} from 'react';
import {useSelector,useDispatch} from 'react-redux'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import {Typography,Box,IconButton,Tooltip} from '@material-ui/core/';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Icon from '@material-ui/core/Icon';
import { hostsSelector,setCurrentHost,initialState,updateHost,tablesSelector,fetchHosts,fetchTables} from '../../Store/modules/hostStore';
import {currentEventSelector} from '../../Store/modules/evntStore'


const useStyles = makeStyles({
  root: {width: '100%',},
  container: {maxHeight: 430,},
  rotate :{transform: 'rotate(90deg)'},
  noRotate :{transform:'rotate(0deg)'}

});

const ListInvit = (props) => {

    const classes = useStyles()
    const dispatch = useDispatch()

    const event = useSelector(currentEventSelector)

    const hostss = useSelector(hostsSelector)

    const tables = useSelector(tablesSelector)

    let arr = []

    tables.forEach(table => {

        arr = [...arr, ...table.arrayTable]
    })

    const  hosts = hostss.concat(arr)

   React.useEffect(() => {

    dispatch(fetchHosts(event.eventId))
    dispatch(fetchTables(event.eventId))

  },[dispatch,event.eventId])




    const [transform,setTransform] = useState(false)

    const toggleSortList = () => !transform ? setTransform(true) : setTransform(false)

    const selectHost = (host) => {

        let hostUpdated = {...initialState.currentHost, ...host}

        dispatch(updateHost(hostUpdated))
        dispatch(setCurrentHost(hostUpdated))
        props.toggleFormInvit(true) 
    }

   
    return(

        <Paper className={classes.root}>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead> 
                        <TableRow>
                            <TableCell>
                                <Box display="flex" alignItems="center" >
                                    <Typography color="primary" variant="h6">Liste des invit√©s</Typography>
                                    <Tooltip title="ordre alphab√©tique">
                                        <IconButton onClick={toggleSortList}  >
                                            <Icon color="primary" className={!transform ? classes.rotate : classes.noRotate} >chevron_right</Icon>
                                        </IconButton>
                                    </Tooltip>
                                </Box>
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {hosts.map((host,index) => 

                        <TableRow key={index} >
                            <TableCell size="small" align="justify">
                                <Box display="flex" justifyContent="space-between" alignItems="center">
                                    <Box >
                                        <Typography>{host.nom}<span style={{marginLeft:"5px"}}>{host.prenom}</span></Typography>
                                    </Box>
                                    <Box>
                                        <Tooltip title="Selectionner invit√©">
                                            <IconButton onClick={()=> selectHost(host)} color="secondary" size="small"><Icon>add_circle_outlined</Icon></IconButton>
                                        </Tooltip>
                                    </Box>
                                </Box>
                            </TableCell>
                        </TableRow>
                        
                        )}

                    </TableBody>
                </Table>


            </TableContainer>
        </Paper>
    )
}

export default ListInvit