import React,{useState,useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import {Typography,Box,Paper,Switch,TextField,IconButton,Button} from '@material-ui/core/';
import Icon from '@material-ui/core/Icon';
import {currentHostSelector,setCurrentHost,initialState} from '../../Store/modules/hostStore'
import InvitAutreForm from './InvitAutresForm'



const InvitForm = (props) => {

    const dispatch = useDispatch()

    const host = useSelector(currentHostSelector)
    
    const [hostCopy,setHostCopy] = useState(host) 
    
    const [openInvitAutre,setOpenInvitAutre] = useState(false)

    const [editMode,setEditMode] = useState(false)

    const toggleOpenInvitAutre = (val) => setOpenInvitAutre(val)

   
    useEffect(() => {

        setHostCopy(host)

    },[host])
    
    const handleChangeSwitch = (e) => {

        const value = e.target.checked
       
      setHostCopy({...hostCopy,autreMembre:value})
    }

    const switchEditMode = (val) => setEditMode(val)

    const handleChange = (e) => {

        const {name,value} = e.target

        setHostCopy({...hostCopy,[name]:value})
    }
   
    const handleSubmit = () => { 

        if(hostCopy.autreMembre && !hostCopy.autreMembreCompleted){
            
            switchEditMode(false)
           dispatch(setCurrentHost(initMembreArray()))
           toggleOpenInvitAutre(true)
           
        } else if(hostCopy.autreMembre && hostCopy.autreMembreCompleted){

            switchEditMode(true)
            dispatch(setCurrentHost(hostCopy))
            toggleOpenInvitAutre(true)
        }

    } 

    const initMembreArray = () => {

        let membre = {}
        let arr = [];
        let copyObj = {...hostCopy}
    
        for(let i = 1 ; i < parseFloat(hostCopy.nbMembre)+1; i++){
    
            membre = {...initialState.currentMembre,membre:"membre " + i}
            arr = [...arr,membre]
        }
        
        copyObj = {...copyObj,membres:arr}

        return copyObj
    }

    return(

        <Paper elevation={2}>
            <Box display="flex" flexDirection="column" p={2}>
               { openInvitAutre && <InvitAutreForm editMode={editMode} toggleOpenInvitAutre={toggleOpenInvitAutre} toggleFormInvit={props.toggleFormInvit}/>} 
                <Box>
                    <Typography color="secondary" variant="h4">Inviter</Typography>
                </Box>
                <Box  mt={2} display="flex" flexDirection="column">
                    <TextField label="Nom" value={hostCopy.nom}/>
                    <TextField label="Pr√©nom" value={hostCopy.prenom}/>
                </Box>
                {!hostCopy.autreMembreCompleted ? (

                <Box mt={2}>
                    <Typography color="textSecondary" style={{fontSize:"0.8em"}}>Ajouter d'autres membres √† l'invitation ?</Typography>
                    <Switch onChange={handleChangeSwitch} checked={hostCopy.autreMembre}  color="primary" size="small"/>
                    { hostCopy.autreMembre && 
                    <Box mt={1}>
                        <TextField
                        onChange={handleChange}
                        type="number"
                        InputProps={{ inputProps: { min: 0, max: 5 }}}
                        label="Nombre de membres"
                        helperText="max 5 autres personnes"
                        color="primary"
                        value={hostCopy.nbMembre}
                        name="nbMembre"
                        />
                    </Box>
                    } 
                </Box>
                ) : (<Box mt={2}>
                        <Typography style={{fontSize:"0.8em"}}>{hostCopy.membres.length} <span> Membre(s) enregistr√©(s) pour cette invitation</span></Typography>
                        <Typography onClick={() => handleSubmit()} style={{fontSize:"0.8em"}}>Effectuer des changement parmi les membres ? </Typography>
                    </Box>)}
                <Box mt={2} display="flex" justifyContent='space-between' alignItems='center'>
                    <Box>
                        <IconButton size="small" onClick={()=> props.toggleFormInvit(false)}><Icon color="primary">close</Icon></IconButton>
                    </Box>
                    <Box>
                        <Button onClick={() => handleSubmit()} color="primary">{hostCopy.autreMembreCompleted ? "MODIFIER" : "VALIDER" }</Button>
                        <Button onClick={() => setHostCopy(host)} color="primary">Annuler</Button>
                    </Box>
                </Box>
            </Box>

        </Paper>
    )
}

export default InvitForm