import React, { useState } from 'react'
import {useDispatch} from 'react-redux'
import {updateHost} from '../../Store/modules/hostStore'
import { List,ListItem,Box,Typography,FormControl,InputLabel,Select,MenuItem,Button} from '@material-ui/core'


const ListMembre = (props) => {

    const [copyHost,setCopyHost] = useState(props.host)

    const dispatch = useDispatch()

    const handleChange = (e,id) => {

      setCopyHost({...copyHost,membres : copyHost.membres.map(item => item.membre === id ? {...item,presence:e.target.value} : item)})
    }

    const handleSubmit = () => {

        dispatch(updateHost(copyHost))
    }

    return(

        <List dense>
             {copyHost.membres.map((membre,index) => 

                <ListItem dense divider key={index}>
                    <Box display="flex" justifyContent="space-between" style={{width:"100%"}}>
                        <Box display="flex" alignItems="center">
                            <Box mr={1}><Typography style={{fontSize:"0.9em"}} >{membre.nom}</Typography></Box>
                            <Typography style={{fontSize:"0.8em"}} >{membre.prenom}</Typography>
                        </Box>
                        <Box>
                            <FormControl style={{minWidth:"120px"}} margin="dense">
                                <InputLabel id="demo-simple-select-label">pr√©sence</InputLabel>
                                <Select
                                value={membre.presence}
                                onChange={(e)=>handleChange(e,membre.membre)}
                                >
                                    <MenuItem  value="√† confirmer" ><Typography variant="body2" >PEUT-ETRE</Typography></MenuItem>
                                    <MenuItem  value="absent"><Typography variant="body2" >ABSENT</Typography></MenuItem>
                                    <MenuItem  value="pr√©sent"><Typography variant="body2" >PRESENT</Typography></MenuItem>
                                </Select>
                            </FormControl>
                        </Box>
                    </Box>
                </ListItem> 
            )}   
            <Box display="flex" ml={1} mt={1} alignItems="center">
                <Button onClick={handleSubmit} size="small" color="primary">valider</Button>
                <Button onClick={()=> setCopyHost(props.host)} size="small" color="primary">annuler</Button>
            </Box>              
        </List>
    )
}


export default ListMembre