import React from 'react'
import { Grid, Typography,Button,Box} from '@material-ui/core'
import Icon from '@material-ui/core/Icon'
import {Link} from 'react-router-dom'
import {useSelector} from 'react-redux'
import {eventsSelector} from '../Store/modules/evntStore'
import {loadingSelector} from '../Store/modules/authStore'
 import CardEvent from '../Components/Dashboard/CardEvent'


const Dashboard = () => {

    const events = useSelector(eventsSelector)
    const loading = useSelector(loadingSelector)
    
    const [displayStyle,setDisplayStyle] = React.useState("none")

    const [flagEvents,setFlagEvents] = React.useState(false)

    React.useEffect(()=> {

        events.length > 0 ? setFlagEvents(true) : setFlagEvents(false)

        setTimeout(() => {

            setDisplayStyle("flex")

        }, 1000);

    },[events])


    return(
        <div>
            {loading && 
            <div>
                { flagEvents ? (
                    <Box display='flex' justifyContent='center'>
                        <Grid item md={10} >
                            <CardEvent/>
                        </Grid>             
                    </Box>
                ) : (
                <Box display='flex' justifyContent='center'>
                    <Box mt={5} style={{display:displayStyle}} flexDirection="column" alignItems="center" >
                        <Typography variant="h6" className='textColor'>Vous n'avez pas cr√©e d'√©venements pour le moment</Typography>
                        <Box mt={2}>
                            <Link className='text-link' to='/Evenement'>
                                <Button endIcon={<Icon>directions</Icon>} size="large" color='primary'>Cr√©er un √©venement</Button>
                            </Link>
                        </Box>
                    </Box>
                </Box>)}
          </div>}
    </div>)
}

export default Dashboard