import React,{useState} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import {Box, Typography,Button,IconButton,Dialog,DialogContent,DialogActions, TextField}from "@material-ui/core/"
import Icon from "@material-ui/core/Icon"
import {useStyles} from '../../ConfigMaterial'
import {currentHostSelector,updateHost,setCurrentHost,initialState} from '../../Store/modules/hostStore'


const InvitAutresForm = (props) => {

    const host = useSelector(currentHostSelector)
    const dispatch = useDispatch()

    const classes = useStyles()

    const [copyHost,setCopyHost] = useState(host)

    const [isValid,setIsValid] = useState(true)

    const [titre, setTitre] = useState("")

    React.useEffect(() => {

        !props.editMode ? setTitre('Saisir les donn√©es des autres membres') : setTitre('Effectuez vos modifications parmi les membres')

    },[props.editMode])


    const handleChange = (e,id) => {
        
        const {name,value} = e.target

        setCopyHost({...copyHost ,membres: copyHost.membres.map(item => item.membre === id ? {...item,[name]:value} : item)})
    }

    const handleAddMembre = () => {

      let membre = `membre ${copyHost.membres.length + 1}` 

        setCopyHost({...copyHost,membres: [...copyHost.membres,{...initialState.currentMembre,membre:membre}]})
    }

    const handleDeleteMembre = (id) => {

        setCopyHost({...copyHost,membres: copyHost.membres.filter(item => item.id !== id)})
        
    }

    const handleSubmit = () => {

      copyHost.membres.forEach(membre => {

        if(membre.nom !== "" || membre.prenom !== ""){

            let copyObj = {...copyHost,autreMembreCompleted:true}
           
            dispatch(updateHost(copyObj))
            dispatch(setCurrentHost(copyObj))
            props.toggleOpenInvitAutre(false)
            props.toggleFormInvit(false)

        } else {

          setIsValid(false)

          setTimeout(() => {
              
            setIsValid(true)
          }, 3000);

        } 
    })
}
    
    return(
        <div>
            <Dialog open={true} disableBackdropClick disableEscapeKeyDown>
                <Box p={2} display="flex" flexDirection="column" justifyContent="center">
                    <Box mt={1}>
                        <Typography color="primary" variant="h5">{titre}</Typography>
                    </Box>
                    {props.editMode &&  
                    <Box mt={2}>
                        <Button onClick={()=>handleAddMembre()} color="secondary" startIcon={<Icon>add_circle</Icon>}>ajouter membre</Button>
                    </Box>}
                    <DialogContent>  
                    {copyHost.membres.map((item,index) => 
                
                    <Box key={index} display="flex" flexDirection="column">
                        <Box mt={3}>
                            <Typography style={{fontSize:"0.9em"}}>{item.membre}</Typography>
                        </Box>
                        <TextField onChange={(e) =>handleChange(e,item.membre)}  name="nom" value={item.nom} margin="dense"  label="Nom" color="primary"/>
                        <TextField onChange={(e) =>handleChange(e,item.membre)} name="prenom" value={item.prenom} margin="dense" label="Pr√©nom" color="primary"/>
                        {props.editMode &&
                        <Box>
                            <Button color="primary" size="small" onClick={()=>handleDeleteMembre(item.id)}  startIcon={<Icon size="small">delete_outlined</Icon>}>supprimer membre</Button>
                        </Box> }
                    </Box>  
                    )}
                    {!isValid && 
                    <Box mt={1}>
                        <Typography className={classes.echec}>Veuillez compl√©ter tout les champs</Typography>
                    </Box>
                    }    
                    </DialogContent>
                    <DialogActions> 
                        <Box mt={2} display="flex" justifyContent="space-between" style={{width:"100%"}} alignItems="center">
                            <Box>
                                <IconButton onClick={()=> props.toggleOpenInvitAutre(false)}><Icon color="primary">close</Icon></IconButton>
                            </Box>
                            <Box>
                                <Button onClick={handleSubmit} color="secondary">Enregistrer</Button>
                                <Button onClick={()=> setCopyHost(host)} color="secondary">Annuler</Button>
                            </Box>
                        </Box>
                    </DialogActions>
                </Box>
            </Dialog>
        </div>
    )

}

export default InvitAutresForm